.table
    padding: 20px
    border: 1px solid lightgray
    @media (max-width: 1100px)
        border: 1px solid white
        border-radius: 5px
        margin: 40px 40px 0 40px
        padding: 0 0 0 0

.row-head
    display: flex
    @media (max-width: 1100px)
        display: flex 
        flex: 100%
        flex-wrap: wrap
        text-align: center
        border: 1px solid lightgrey
        border-radius: 5px
        margin-bottom: 1em

.row-head-company-name
    display: flex
    @media (max-width: 1100px)
        display: flex 
        flex: 100%
        flex-wrap: wrap
        justify-content: center
        text-align: center
        border: 1px solid lightgrey
        border-radius: 5px
        padding-top: 10px

.row
    display: flex
    @media (max-width: 1100px)
        display: grid 
        gap: 1rem
        grid-template-columns: 50% 50%
        word-wrap: break-word  

.item-head
    padding: 10px 0px
    width: 25%
    @media (max-width: 1100px)
        width: 100%
        padding: 0
        margin-top: 1em

.item
    padding: 10px 0px
    width: 25%
    @media (max-width: 1100px)
        width: 100%
        font-size: 0.9em

.title
    font-weight: 600

.status-ISSUED
    border: 2px solid green
    border-radius: 5px
    color: green
    text-align: center
    font-weight: 600
    justify-content: center
    padding: auto 0
    @media (max-width: 1100px)
        padding: 1em 0
        border: 0
    

.status-LAPSED
    border: 2px solid #CE0000
    border-radius: 5px
    color: #CE0000
    text-align: center
    font-weight: 600
    @media (max-width: 1100px)
        padding: 1em 0
        border: 0

.status-SUBMITTED
    border: 2px solid #2B3956
    border-radius: 5px
    color: #2B3956
    text-align: center
    font-weight: 600
    @media (max-width: 1100px)
        padding: 1em 0
        border: 0

.subvalues
    margin-top: 15px
    border-top: 1px solid lightgray
    padding-top: 15px
    @media (max-width: 1100px)
        border-top: 0